"use client"
import { useState } from 'react';
import style from '../../CSS/userSide/profile.module.css';
import { useNavigation } from '../../Context/NavigationContext';
import { addAddress } from '../../services/clientServices/AddressService';
export default function addAddress() {
    const { userData } = useNavigation();
    const [address, setAddress] = useState({
        addressId: null,
        district: '',
        province: '',
        municipality: '',
        wardNumber: '',
        landMark: '',
        userId: userData.userId
    });

    const handleAddressChange = (e) => {
        const { name, value } = e.target;
        setAddress(prev => ({
            ...prev,
            [name]: name === 'wardNumber' ? (value ? parseInt(value, 10) : '') : value
        }))
    }
    const submitAddress = async () => {
        try {
            const addressDTO = {
                district: address.district,
                province: address.province,
                municipality: address.municipality,
                wardNumber: address.wardNumber ? parseInt(address.wardNumber,10): null,
                landmark: address.landMark,
                userId: userData.userId
            }
            const response = await addAddress(addressDTO);
            setAddress({})
        } catch (err) {
            console.log("error in handleAddAddress", err.response.data);
        }
    }
    return (
        <div className={style.addressFormContainer}>
            <div className={style.title}>
                <h3>Address Form</h3>
            </div>
            <div className={style.addressFormGroup}>
                <div className={style.formGroup}>
                    <label htmlFor="district">District</label>
                    <input
                        type="text"
                        name="district"
                        id="district"
                        value={address.district}
                        onChange={handleAddressChange}
                        placeholder="Enter district"
                    />
                </div>
                <div className={style.formGroup}>
                    <label htmlFor="province">Province</label>
                    <input
                        type="text"
                        name="province"
                        id="province"
                        value={address.province}
                        onChange={handleAddressChange}
                        placeholder="Enter a province"
                    />
                </div>
                <div className={style.formGroup}>
                    <label htmlFor="municipality">Municipality</label>
                    <input
                        type="text"
                        name="municipality"
                        id="municipality"
                        value={address.municipality}
                        onChange={handleAddressChange}
                        placeholder="Enter municipality name"
                    />
                </div>
                <div className={style.formGroup}>
                    <label htmlFor="wardNumber">Ward Number</label>
                    <input
                        type="number"
                        name="wardNumber"
                        id="wardNumber"
                        value={address.wardNumber}
                        onChange={handleAddressChange}
                        placeholder="Enter ward number"
                        min="1"
                        max="35" // Typical ward number range in Nepal
                    />
                </div>
                <div className={style.formGroup}>
                    <label htmlFor="landMark">LandMark</label>
                    <input
                        type="text"
                        name="landMark"
                        id="landMark"
                        value={address.landMark}
                        onChange={handleAddressChange}
                        placeholder="Enter a landMark"
                    />
                </div>
                <button onClick={submitAddress} className={style.submitButton}>
                    Add Address
                </button>
            </div>
        </div>
    )
}